.error404{
  position: relative;
  &__img{
    img{
      width: 100%;
      display: block;
    }
    ._view2,
    ._view3{
      display: none;
    }
  }
  &__inner{
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    padding: 7.81em 21.19em;
    width: 1440px;
    max-width: 100%;
    z-index: 2;
  }
  &__title{
    font-weight: 700;
    margin-bottom: 1.19em;
    &-big{
      font-size: 5em;
      line-height: 110%;
    }
    &-small{
      font-size: 4.0625em;
      line-height: 110%;
    }
  }
  &__desc{
    max-width: 24em;
  }
  &__nav{
    display: flex;
    margin-top: 2em;
  }
  &__btn{
    width: 11.5625em;
    height: 3em;
    border-radius: 0.5em;
  }
}


@media screen and (max-width: 1024px){
  .error404{
    padding-bottom: 23.3em;
    &__img{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    &__inner{
      padding: 1.56em 1.56em 8.31em;
      top: unset;
      bottom: 0;
    }
  }
}


@media screen and (max-width: 576px){
  .error404{
    padding-bottom: 4.375em;
    &__img{
      ._view2{
        display: none;
      }
      ._view3{
        display: block;
      }
    }
    &__inner{
      padding: 9.25em .75em 2em;
      top: 0;
      bottom: unset;
      text-align: center;
    }
    &__title{
      padding: 0 1em;
      margin-bottom: 1.12em;
      &-big,
      &-small{
        font-size: 2.75em;
        display: inline;
      }
    }
    &__nav{
      margin-top: 1.5em;
    }
    &__btn{
      width: 100%;
    }
  }
}
